<template>
  <div class="pd20">
    <div class="navigate-bar">
      <span>测试对接管理</span>
      <el-button v-show="tableData.length == 0" style="float:right;margin-top:10px;" type="primary" size="mini" @click="dialog.show = true">测试账号申请</el-button>
    </div>
    <div class="table-wrapper">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="name" label="项目名称" />
        <el-table-column prop="approved" label="状态">
          <div slot-scope="scope">
            <el-tag v-if="scope.row.approved == 0" type="warning">待审核</el-tag>
            <el-tag v-if="scope.row.approved == -1" type="danger">审核失败</el-tag>
            <el-tag v-if="scope.row.approved == 1" type="info">测试中</el-tag>
            <el-tag v-if="scope.row.approved == 2" type="success">测试通过</el-tag>
            <el-tag v-if="scope.row.approved == -2" type="danger">测试失败</el-tag>
          </div>
        </el-table-column>
        <el-table-column prop="account" label="账号" :formatter="formatter" />
        <el-table-column prop="secretKey" label="密钥" :formatter="formatter" />
        <el-table-column prop="dataAddr" label="数据提交地址" :formatter="formatter" />
      </el-table>
    </div>
    <el-dialog title="添加测试账号" center append-to-body :visible.sync="dialog.show" width="600px"
      :before-close="resetDialog">
      <el-form label-position="right" label-width="190px" :model="dialog.form" ref="form" style="width:500px;">
        <el-form-item label="测试项目名称" prop="projectName" verify>
          <el-input v-model="dialog.form.projectName" />
        </el-form-item>
        <el-form-item label="项目地址" prop="projectAddr" verify>
          <el-input v-model="dialog.form.projectAddr" />
        </el-form-item>
        <el-form-item label="总承包名称" prop="contractName" verify>
          <el-input v-model="dialog.form.contractName" />
        </el-form-item>
        <el-form-item label="总承包统一社会信用代码" prop="contractCreditCode" verify>
          <el-input v-model="dialog.form.contractCreditCode" maxLength="18" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog.show = false">取 消</el-button>
        <el-button type="primary" @click="handleSubmit" :loading="dialog.confirmLoading">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      searchForm: {},
      tableData: [],
      dialog: {
        show: false,
        form: {
          projectName: '',
          projectAddr: '',
          contractName: '',
          contractCreditCode: '',
        },
        confirmLoading: false
      }
    }
  },
  beforeMount () {
    this.getTableData()
  },
  methods: {
    formatter (row, column, cellValue, index) {
      return cellValue || '无'
    },
    getTableData () {
      (async () => {
        try {
          let res = await this.$ajax.post('/test/accountFront/getAccountTestListByCompany')
          this.tableData = res.result || []
        } catch (error) {
          this.$message.error(error.msg || '获取表格数据失败')
        }
      })()
    },
    handleSubmit () {
      (async () => {
        try {
          let valid = await this.$refs.form.validate()
          this.dialog.confirmLoading = true
          let res = await this.$ajax.post('/test/accountFront/add', this.dialog.form)
          this.$message.success('提交申请成功')
          this.dialog.confirmLoading = false
          this.dialog.show = false
          this.getTableData()
        } catch (error) {
          console.log(error)
          if (typeof error == 'boolean') {
            // this.$message.error('请先修复表单错误')
            return
          }
          this.dialog.confirmLoading = false
          this.$message.error(error.msg || '提交失败')
        }
      })()
    },
    resetDialog (done) {
      this.$refs.form.resetFields()
      done()
    }
  }
}
</script>
